import React, { useEffect, useState } from 'react';
import "./PartnershipRegistration.css";
import { useNavigate, useParams } from "react-router-dom";
import axios from 'axios';
import MyForm from '../components/DbForm';


const PartnershipRegistration = () => {
  
    let navigate = useNavigate();
  var [myvalue, setmyValue] = MyForm({ name: "", email: "",mobileno:"", firm: "",address: ""})
  const addPartner = () => {
      console.log(myvalue);
      
      axios.post("/api/partner/addpartner", myvalue);
      axios.post("/api/partner/sendmail", myvalue);
      alert("Successfully Registered!!!!You will receive an email regarding more details.Thank you.")
      navigate("../partnership", { replace: true })
          
      
  }
    

    return (
        <div className='reg'>
            <h1 align="center" className='head'>MARKETING PARTNER APPLICATION FORM</h1>
            <form>
                <label for="name">Name</label>
                <input type="text" className='textfields' name="name"   value={myvalue.name} onChange={setmyValue} required  />

                <label for="email">Email</label>
                <input type="email" className='textfields' name="email" value={myvalue.email} onChange={setmyValue} required />

                <label for="mobileno">Mobile Number</label>
                <input type="text" className='textfields' name="mobileno" value={myvalue.mobileno} onChange={setmyValue} required />

                <label for="Firm">Name of the Firm</label>
                <input type="text" className='textfields' name="firm" value={myvalue.firm} onChange={setmyValue} required />
                
                 <label for="address">Address</label>
                <input type="text" className='textfields' name="address" value={myvalue.address} onChange={setmyValue} required />

                <input type="submit" value="Register" className='register'   onClick={addPartner} />
            </form>

        </div>
    );
};

export default PartnershipRegistration;