import React, { useState, useEffect } from 'react';
import axios from 'axios';
// import moment from "moment";
import * as FileSaver from "file-saver";
import * as XLSX from "xlsx";
import "./PartnershipAdmin.css";
import { useNavigate } from "react-router-dom";

const PartnershipAdmin = () => {
    let navigate = useNavigate();
    let [Data, SetData] = useState([]);
    useEffect(() => {
        registerData();
    }, []);

    function registerData() {
        axios.get("/api/partner/viewpartner")
            .then((response) => {
                console.log(response.data);
                SetData(Data = response.data);
            }
            )
    }

   
    function deleteReg(event) {
        var pid = event.target.getAttribute("name");
        axios.post("/api/partner/deletepartner", { _id: pid })
            .then((res) => {
                alert("Successfully Deleted");
                console.log(res.data);
                navigate("../admin/partnership", { replace: true })
            })
    }


    //save to excel
    const fileType =
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
    const fileExtension = ".xlsx";
    const fileName = "partnershiplist"
    const exportToCSV = (Data, fileName) => {
        const ws = XLSX.utils.json_to_sheet(Data);
        const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
        const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
        const data = new Blob([excelBuffer], { type: fileType });
        FileSaver.saveAs(data, fileName + fileExtension);
    };

    return (
        <div className="RegContainer">
            <button className='download' onClick={(e) => exportToCSV(Data, fileName)} >Download as excel</button>
            <h2 className='coursehead'>List of partnership firms</h2>
            <div class="row">
                <div class="col-lg-11 mx-auto">
                    <div class="card border-0 shadow">
                        <div class="card-body p-5">
                            <div class="table-responsive">
                                <table class="table m-0">
                                    <thead>
                                        <tr>
                                            <th scope="col">Name</th>
                                            <th scope="col">Email</th>
                                            <th scope="col">Mobile No</th>
                                            <th scope="col">Firm</th>
                                            <th scope="col">Address</th>
                                            <th scope="col"></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            Data.map((res, key) => (

                                                <tr>

                                                    <th scope="row">{res.name}</th>
                                                    <td>{res.email}</td>
                                                    <td>{res.mobileno}</td>
                                                    <td>{res.firm}</td>
                                                    <td>{res.address}</td>
                                                    <td>

                                                        <ul className="list-inline m-0">
                                                            <li className="list-inline-item" id="ud">
                                                                <button className="btn btn-danger btn-sm rounded-0" name={res._id} onClick={deleteReg} type="button" data-toggle="tooltip" data-placement="top" title="Delete"><i class="fa fa-trash" name={res._id}></i></button>
                                                            </li>
                                                        </ul>
                                                    </td>
                                                </tr>
                                            ))}

                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PartnershipAdmin;