import React, { useEffect, useState } from 'react';
import "./PartnershipRegistration.css";
import { useNavigate, useParams } from "react-router-dom";
import axios from 'axios';
import MyForm from '../components/DbForm';


const MembershipReg = () => {
  
    let navigate = useNavigate();
  var [myvalue, setmyValue] = MyForm({ name: "", email: "",mobileno:"", firm: "",address: ""})
 
    

    return (
        <div className='reg'>
            <h1 align="center" className='head'>MEMBERSHIP REGISTRATION FORM</h1>
            <form>
                <label for="name">Name Of Organization</label>
                <input type="text" className='textfields' name="name"   required  />

                <label for="website">Website</label>
                <input type="text" className='textfields' name="website" required />

                <label for="address">Address</label>
                <input type="text" className='textfields' name="address" required />

                <label for="Cin">Corporate Identity Number</label>
                <input type="text" className='textfields' name="cin" required />
                
                 <label for="gstn">GSTN</label>
                <input type="text" className='textfields' name="gstn" required />

                <label for="toc">Type of Company</label>
                <input type="text" className='textfields' name="toc" required />

                <label for="doi">Date of Incorporation</label>
                <input type="text" className='textfields' name="doi" required />

                <input type="submit" value="Register" className='register' />
            </form>

        </div>
    );
};

export default MembershipReg;