import React from 'react';
import logo from '../../assets/images/logo.png';
import '../../css/style.css';
import { Link, useNavigate } from "react-router-dom";
import Dropdown from 'react-bootstrap/Dropdown';


function NavbarAdmin(props) {
  let navigate = useNavigate();
  function logout() {
    props.setlogin(0);
    navigate("../", { replace: true })
  }
  return (

    <nav class="navbar navbar-expand-lg bg-white navbar-light shadow sticky-top p-0">
      <a href="/" class="navbar-brand d-flex align-items-center px-4 px-lg-5">

        <img src={logo} style={{ width: '50px', height: '50px', marginRight: '10px' }}></img>
      </a>
      <div class="content">
        <h2>ICTAK</h2>
        <h2>ICTAK</h2>
      </div>

      <button type="button" class="navbar-toggler me-4" data-bs-toggle="collapse" data-bs-target="#navbarCollapse">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarCollapse">
        <div class="navbar-nav ms-auto p-4 p-lg-0">

          <Link to="/" class="nav-item nav-link">Home</Link>
          <Link to="/about" class="nav-item nav-link">About</Link>
          <Link to="/courses" class="nav-item nav-link">Courses</Link>

          <Dropdown>
            <Dropdown.Toggle variant="" id="dropdown-basic">
              Offerings
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item ><Link to="/partnership" className="dropdown-item">Partnership</Link></Dropdown.Item>
              <Dropdown.Item><Link to="/membership/academic" className="dropdown-item">Academic Membership</Link></Dropdown.Item>
              <Dropdown.Item><Link to="/membership/corporate" className="dropdown-item">Corporate Membership</Link></Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
          <Link to="/events" class="nav-item nav-link">Events</Link>
          <Link to="/contact" class="nav-item nav-link">Contact</Link>
          <Link to="//retail.ictkerala.org/" target="_blank" class="nav-item nav-link">Paatshala LMS</Link>
          <Link to="/admin/courses" class="nav-item nav-link">Admin Dashboard</Link>
        </div>

        <Link to="" class="nav-item nav-link" onClick={logout}>Logout</Link>
      </div>
    </nav>
  )
}

export default NavbarAdmin